import React, { Component } from "react";
import "./Banner.scss";

class Banner extends Component {
  constructor(props) {
    super(props);
    this.handleButtonClick = this.handleButtonClick.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
  }
  handleButtonClick() {
    const targetElem = document.querySelector('.md-grid');
    console.log(targetElem.offsetTop);
    window.scrollTo(0, targetElem.offsetTop - 64);
  }
  handleKeyPress() {
    this.handleButtonClick();
  }
  render() {
    return (
      <div className="banner">
        <div className="content">
          <hgroup>
            <h1>EqualD ONEDAY CLASS × CAFE LUXEMBROUG</h1>
            <p>7th - 31st, JAN. 2019</p>
          </hgroup>

        </div>
        <button className="button" onClick={this.handleButtonClick} onKeyPress={this.handleKeyPress}>
          Class List ▼
        </button>
      </div>
    )
  }
}

export default Banner;



